import { Fragment } from "react";
import { Helmet } from "react-helmet-async";

import Notfound from "Components/Error/404";

const NotfoundPage = () => (
  <Fragment>
    <Helmet>
      <title>404 Page Not Found</title>
    </Helmet>
    <Notfound />
  </Fragment>
);

export default NotfoundPage;
